$('.searchForm').on('submit', e => {
    return startSearch()
})
$('.icon-search').on('click',e => {
   startSearch()
})

function startSearch() {
    let val = $('.search input').val().trim()
    if('' === val){
        return false
    }
    window.open(`https://baidu.com/s?wd=${val}`)
    return false
}

// Ëé∑ÂèñÁ´ôÁÇπ logo Âú∞ÂùÄ
$('.addBtn').click(e => {
    let src = prompt('ËØ∑ËæìÂÖ•Ë¶ÅÊ∑ªÂä†ÁöÑÂú∞ÂùÄ!')
    let spanStr
    let urlObject = getLocalStorage()
    urlObject = urlObject === null ? {} : urlObject
    if(src){
        if(checkChinese(src)) return alert('ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÂüüÂêçÔºÅ')
        src = src.trim().toLowerCase()
        if(!src.startsWith('https://')){
            src = 'https://' + src
        }
        spanStr = src.slice(8).split('.')
        spanStr = spanStr.length > 2 ? spanStr[1] : spanStr[0]
        // ÊòØÂê¶ÊúâÁõ∏ÂêåÁöÑÈìæÊé•Â≠òÂú®
        for(key in urlObject){
           if(compare(urlObject[key],src)){
               alert('ÈìæÊé•Â∑≤Â≠òÂú®')
               return
           }
        }
        urlObject[spanStr] = src
        setLocalStorage(urlObject)
        createElement(src,spanStr)
    }
})

// ‰∏≠ÊñáÊ£ÄÊµã
function checkChinese(val){
    var reg = new RegExp("[\\u4E00-\\u9FFF]+","g");
    if(reg.test(val)){
        return true
    }else {
        return false
    }
}

// 1„ÄÅÈöêËóè li
// 2„ÄÅÂà†Èô§ localStorage ‰∏≠ÂØπÂ∫îÁöÑÈîÆ
$('.list > ul').on('click', '.icon-close', e => {
    let isTouchDevice = 'ontouchstart' in document.documentElement
    if(isTouchDevice){
        $(e.target).parents('li').hide('slow')
    }else {
        $(e.target).parents('li').fadeOut('slow')
    }

    // ÂêéÁª≠Êõ¥Êñ∞spanÁöÑÂÄº‰ª•Áî®Êà∑ËæìÂÖ•ÁöÑ‰∏∫‰∏ªÔºåÊâÄ‰ª•Âèñ background-image ÁöÑÂÄºÊù•Âà§Êñ≠
    let value = $(e.target).parents('div').children('.bgc-div').css('backgroundImage')
    // Áõ¥Êé•Ëé∑ÂèñÁöÑÂÄº‰∏∫ url("xxxx") ÁöÑÂ≠óÁ¨¶‰∏≤,Ë¶Å‰ªé " ÂºÄÂßãÂàÜÂâ≤
    value = value.split("\"")[1].split("\"")[0];
    value = value.slice(0,-12)
    let key = findKey(value,compare)
    let urls = getLocalStorage()
    key && delete urls[key] && setLocalStorage(urls)
    e.stopPropagation()
})

let compare = (a,b) => a === b
function findKey(value,compare) {
    let urls = getLocalStorage()
    for(key in urls){
        if(compare(urls[key],value)){
            return key
        }
    }
}

function setLocalStorage(value){
   localStorage.setItem(('urls'),JSON.stringify(value))
}

function getLocalStorage(){
    return JSON.parse(localStorage.getItem('urls'))
}

let isCloseBtnShow = false
let startTime
let endTime
let timeStep = 0

// ÁßªÂä®Á´ØÈïøÊåâÊòæÁ§∫Âà†Èô§ÊåâÈíÆ
$('.list > ul').on({
    'touchstart':function(e) {
        startTime = new Date()
    },
    'touchend':function() {
        endTime = new Date()
        let touchTime = endTime - startTime
        if(touchTime > 400){
            showCloseBtn()
        }
    }
})

// PC Á´ØÈïøÊåâÊòæÁ§∫Âà†Èô§ÊåâÈíÆ
$('.list > ul').on('mousedown','li',
    e => {
        if(0 === e.button){
            startTime = new Date()
        }
    }
)

$('.list > ul').on('mouseup','li',e => {
    if(0 === e.button) {
        endTime = new Date()
        timeStep = endTime - startTime
        if(timeStep > 500) {
            showCloseBtn()
        }
    }
})

